.result {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    color: #fff;
    opacity: 0.9;
    font-size: 16px;
    display: flex;
    flex-flow: column;
    max-width: 400px;
    margin: 0 auto;
    width: calc(100% - 60px);
    max-height: calc(100% - 104px);
    overflow: hidden;
    background: rgba(0, 0, 0, .5);

    .icon {
        width: 20px;
        height: 20px;
    }

    .result-main {
        overflow: hidden;
        max-height: 0;
        transition: all .5s ease-in-out;

        &.active {
            max-height: 150px;;
        }

        .color-item {
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    .result-bar {
        background: rgba(0, 0, 0, .6);
        padding: 8px;
        display: flex;
        align-items: center;
    }

    .expand-btn {
        margin-right: 6px;
        display: flex;
        align-items: center;
        transform: rotateZ(0deg);
        transition: all .5s;

        &.active {
            transform: rotateZ(180deg);
        }
    }

    .preview-area {
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}