.panel-wrapper, .panel {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    contain: strict;
}

.icon {
    fill: #fff;
    height: 24px;
    width: 24px;
}

.back-btn {
    position: fixed;
    top:20px;
    left: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
    height: 32px;
    width: 32px;
    cursor: pointer;
    z-index: 99;
}

.side-bar {
    position: fixed;
    border-radius: 25px;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    z-index: 99;
    padding: 2px;
    background: rgba(0, 0, 0, 0.6);

    .item {
        outline: none;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        border-radius: 50%;
        border: none;
        background: none;
        height: 30px;
        width: 30px;
    }
}

@media (min-width: 600px) {

    .back-btn {
        left: 20px;
        height: 46px;
        width: 46px;
    }
}


.magnifier {
    position: fixed;
    top: 5px;
    right: 5px;
    width: 110px;
    height: 110px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid rgba(0, 0, 0, 0.4);

    &.hidden {
        display: none;
    }

    .grid {
        position: absolute;
        top: 0;
        left: 0;
    }
}
