.scrubber {
    position: absolute;
    transform-origin: 50% 50%;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
    height: 32px;
    width: 32px;
    cursor: pointer;
    z-index: 99;
    overflow: hidden;

    svg {
        fill: rgba(255, 255, 255, .8);
    }
}

.horizontal {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    background: #ccc;

    .scrubber {
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
    }
}

.vertical {
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 100%;
    background: #ccc;

    .scrubber {
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);

        svg {
            transform: rotateZ(90deg);
        }
    }
}