.intro {
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
}

.open-image-guide {
    margin-bottom: 0;

    span {
        display: none;
    }

    @media (min-width: 768px) {
        font-size: 46px;
        padding: 0 40px;

        span {
        display: inline-block;
        }
    }
}

.select-button {
    cursor: pointer;
    background: none;
    border: none;
    font: inherit;
    padding: 0;
    margin: 0;
    font-weight: 500;
    color: #61dafb;

    &:focus {
        outline: none;
    }

    &:hover,
    &:focus {
        text-decoration: underline;
    }
}

.demo-tip {
    font-size: 20px;
    margin: 10px auto;

    @media (min-width: 768px) {
        font-size: 36px;
        margin: 18px auto;
    }
}

.demo-pic {
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    margin: 3px;
    display: flex;
    align-items: center;
    padding: 7px;
    font-size: 1.3rem;

    img {
        width: 46px;
        height: 46px;
    }
}

.App-logo {
    // animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
